import Vue from 'vue'
import Router from 'vue-router'
import auth from '@/auth'

import Auth from '@/views/Auth'
import Dashboard from '@/views/Dashboard'
import Home from '@/views/Home'
import Load from '@/views/Load'
import Politica from '@/views/Politica'
import Principal from '@/views/Principal'
import Evaluaciones from '@/views/Evaluaciones'
import Clima from '@/views/Clima'
import Certificaciones from '@/views/Certificaciones'




Vue.use(Router)

var routes = [
  { path: '/auth', name: 'auth', component: Auth, meta: { guestOnly: true } },
  { path: '/dashboard/:id?', name: 'dashboard', component: Dashboard, props:true,meta: { requireAuth: true },
  children:[
    { path: 'principal', name: 'principal', component: Principal, meta: { requireAuth: true }},
    { path: 'evaluaciones', name: 'evaluaciones', component: Evaluaciones, meta: { requireAuth: true }},
    { path: 'clima', name: 'clima', component: Clima, meta: { requireAuth: true }},
    { path: 'certificaciones', name: 'certificaciones', component: Certificaciones, meta: { requireAuth: true }}

  ]},
  { path: '/load', name: 'load', component: Load},
  { path: '/home', name: 'home', component: Home},
  { path: '/politica-privacidad', name: 'politica', component: Politica,meta: { guestOnly: true } },
  { path: '*', redirect: '/auth' }
  
]

export const router = new Router({
  mode: 'hash',
  routes
})

router.beforeEach((to, from, next) => {
  let currentUser = auth.user()
  let requireAuth = to.matched.some(record => record.meta.requireAuth)
  let guestOnly = to.matched.some(record => record.meta.guestOnly)

  if (requireAuth && !currentUser) next('auth')
  else if (guestOnly && currentUser) next('dashboard')
  else next()
}
)



