    created() {
        this.HTMLcontent='<h3>POLÍTICA DE PRIVACIDAD MULTIPERFILES SOCIEDAD ANONIMA</h3> <p>MULTIPERFILES, SOCIEDAD ANÓNIMA, sociedad guatemalteca, de naturaleza mercantil, del domicilio de la ciudad y departamento de Guatemala, en adelante bajo el nombre de la aplicación "Cultura Multigroup" se compromete a proteger su privacidad. A continuación, exponemos como tratamos los datos de nuestros usuarios, y como protegemos su privacidad.</p> <p>Cultura Multigroup recopila información acerca de usted antes de acceder o usar nuestra aplicación móvil, sitio web y otros productos y servicios online (conjuntamente, los “Servicios”) y a través de otras interacciones y comunicaciones que tenga con nosotros. Recopilamos la información que usted nos proporciona directamente, por ejemplo, cuando crea o modifica su cuenta, solicita servicios bajo demanda, se pone en contacto con atención al cliente o cuando se comunica de cualquier otra forma con nosotros. Esta información puede incluir, pero no limitarse a: nombre, correo electrónico, número de teléfono, NIT, dirección postal, foto del perfil, método de pago, los artículos solicitados para servicios de entrega, notas de entrega y otra información que usted elija proporcionar.</p> <p>Información que recopilamos a través de su uso de nuestros Servicios Cuando utiliza nuestros Servicios, recopilamos información sobre usted en las siguientes categorías generales:</p> <ul> <li>Uso e información de preferencias: Recopilamos información sobre cómo usted y los visitantes del sitio interactúan con nuestros Servicios, preferencias expresadas y configuraciones seleccionadas.</li> <li>Información del registro: Cuando usted interactúa con los Servicios, recopilamos los registros del servidor, que pueden incluir información como direcciones IP del dispositivo, fechas y horas de acceso, funciones de la aplicación o las páginas visitadas, bloqueos de la aplicación y otra actividad del sistema, tipo de navegador y el sitio de terceros o servicio que estaba utilizando antes de interactuar con nuestros Servicio.</li> </ul> <p> <b>Uso de información</b>, Podemos usar la información que recopilamos sobre usted para:</p> <ul> <li>Prestar, mantener y mejorar nuestros Servicios como, por ejemplo, proporcionar productos y servicios que usted solicite (y enviar información relacionada), desarrollar nuevas funciones, prestar atención al cliente tanto a los Usuarios como a nuestros colaboradores de Cultura Multigroup, desarrollar opciones de seguridad, autenticar usuarios y enviar actualizaciones de productos y mensajes administrativos.</li> <li>Realizar operaciones internas, como, por ejemplo, prevenir el fraude y el abuso de nuestros Servicios; solucionar los errores de software y problemas de funcionamiento; llevar a cabo análisis de datos, pruebas e investigación, controlar y analizar las tendencias de uso y de actividad.</li> <li>Enviarle comunicaciones que pensamos serán de su interés, incluida la información sobre los productos, servicios, promociones, noticias y eventos de Cultura Multigroup, y cuando esté permitido y de conformidad con las leyes locales aplicables, para tramitar concursos, sorteos u otras entradas de promociones y cumplir con los premios relacionados.</li> <li>Personalizar y mejorar los Servicios, incluyendo proporcionar o recomendar funciones, contenido, conexiones sociales, referencias y anuncios.</li> <li>Cualquier uso que Cultura Multigroup, decida darle a dicha información.</li> </ul> <p><b>Intercambio de información</b>, Podemos compartir la información que recopilamos sobre usted tal como se describe a continuación a través de nuestros Servicios, podemos compartir su información:</p> <ul> <li>Con nuestro personal para que puedan prestar los Servicios que usted solicita. Por ejemplo, compartimos su nombre, foto (si la proporciona), y las ubicaciones de entrega.</li> <li>Con el público en general si envía el contenido a un foro público, por ejemplo, comentarios en blogs, mensajes de redes sociales u otras opciones de nuestros Servicios a disposición del público en general.</li> <li>Con los terceros necesarios contratados por nosotros.</li> <li>Con proveedores, asesores, socios de marketing y otros proveedores de servicios que necesitan acceder a dicha información para llevar a cabo el trabajo en nuestro nombre.</li> <li>Cuando nos veamos obligados a suministrarla para atender una orden legítima o judicial de autoridad competente.</li> <li>En relación con, o durante las negociaciones de, cualquier fusión, venta de activos de la empresa, consolidación o reestructuración, financiación o adquisición de la totalidad o una parte de nuestro negocio o en otra empresa.</li> <li>Si se lo notificamos y usted autoriza que se comparta.</li> <li>De forma agregada y/o anónima que no pueda ser razonablemente utilizada para</li> identificarlo. </ul> <p> <b>Opción de gestión. Información de cuenta</b><br> Usted puede acceder a la información de su cuenta en cualquier momento, así como a solicitar la actualización, modificación y eliminación de la información contenida en ella, de forma gratuita. Si desea cancelar su cuenta, envíenos un mensaje de correo electrónico a info@multigroup.com.<br><br> Tenga en cuenta que en algunos casos podemos retener cierta información sobre usted como lo requiere la ley o para fines comerciales legítimos en la medida permitida por la ley. Por ejemplo, si usted tiene un crédito o deuda pendiente en su cuenta, o si creemos que ha cometido fraude o incumplido nuestros Términos y Condiciones, podemos tratar de resolver el problema antes de eliminar su información.<br><br> <b>Cambios en las Políticas de Privacidad</b>, Multigroup, se reserva el derecho de modificar estas Políticas de Privacidad ocasionalmente. Si hacemos cambios importantes en la forma en que tratamos su información personal, se lo notificaremos a través de los Servicios o por otros medios, como el correo electrónico. El uso continuado de los Servicios después de dicha notificación constituye su consentimiento a los cambios. Le recomendamos que revise periódicamente nuestra Política de Privacidad para tener la información más reciente sobre nuestras prácticas.</p>'  
        },

  }
