<template>
    <div>
      <nav class="navbar navbar-default navbar-fixed-top">
          <div class="container-fluid nvbar">
            <div class="navbar-header">
              <a class="navbar-brand">
                
                <img alt="Brand" class="logo-multi" src="../assets/multi-logo.png" @click="goToPage('principal')">
              </a>
            </div>
            
            <div type="button" class="navbar-controles navbar-btn pull-right">
              <a href="#" @click="logOut" title="Cerrar Sesión">
                <span class="fas fa-power-off"></span>
              </a>
            </div> 
            <div type="button" class="navbar-controles navbar-btn pull-right">
              <img :src="user.photoURL" alt="">
            </div> 
            <div type="button" class="navbar-controles navbar-btn pull-right" style="margin-top:20px">
              <p>{{ user.displayName }}</p>
            </div>
          </div>
      </nav>
      <div>
        <router-view></router-view>
      </div>
      <!-- <div class="footer-dashboard">
          <p>MultiGroup 2019 <span class="far fa-registered"></span> V1.0.0</p>
      </div>   -->
    </div>
</template>

<script>
  import auth from '@/auth'

  export default {
    name: 'Dashboard',
    created(){
      this.$router.push({name:'principal'})
    },
    computed: {
      user() {
        return this.$store.getters['user/user']
      }
      
    },
    methods: {
      logOut() {
        auth.logout();
      },
      goToPage(page){
        //console.log(page);
        this.$router.push({name:page})

      }
    }
  }
</script>

<style scoped>


/* DASHBOARD */

body{
  height: 100vh;
  background-image: url(../assets/m-multigroup.png);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: right bottom;
  background-color: #eee;
}
  .navbar-controles img {
    border:solid 2px #fff;
    border-radius: 50px;
    margin-right: 20px;
    width: 42px;
  }

  .navbar-controles p {
    display: none;
    color: #fff;
    margin: 20px 20px 0px 0px !important;
  }

.nvbar{
    background: #c10d25 !important;
    -webkit-box-shadow: 0px 2px 23px 0px rgba(0,0,0,0.48);
    -moz-box-shadow: 0px 2px 23px 0px rgba(0,0,0,0.48);
    box-shadow: 0px 2px 23px 0px rgba(0,0,0,0.48);
}

.navbar-controles span{
  /* display: none; */
      color: #fff;
      font-size: 3em;
}
.logo-multi{
    width: 125px;
}
.footer, .footer-dashboard {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 100%;
  color: #85898c;
  text-align: center;
}

.footer-dashboard{
  width: 100% !important;
}


.container-logo{
  margin-top: 75px;
  width: 100%;
}

.content{
  padding: 0% 5% 0 5%;
}

/**
 * Circle Styles
 */


 
  


  .button-fiori:active, .button-gc:active, 
  .button-reporting:active, .button-bpc:active,.button-planta:active,
  .button-el:active, .login-button:active, .google-button:active, .button-inc:active{
    transform: translateY(1px);
  }
  .button-fiori:hover, .button-gc:hover, 
  .button-reporting:hover, .button-bpc:hover, .button-planta:hover,
  .button-el:hover, .login-button:hover, .google-button:hover, .button-inc:hover{
    transform: scale(1.1);
    -webkit-transform: scale(1.1);
  }

  .button-fiori:after{
    background-image: url(../assets/p-mensuales-btn.png);
  }
  .button-gc:after{
    background-image: url(../assets/elearning-btn.png);
  }
  .button-reporting:after{
    background-image: url(../assets/cod-etica-btn.png);
  }
  .button-bpc:after{
    background-image: url(../assets/capsulas-btn.png);
  }
  .button-el:after{
    background-image: url(../assets/clima-btn.png);
  }
  .button-inc::after{
    background-image: url(../assets/desempeno-btn.png);
  }
  .button-planta::after{
    background-image: url(../assets/planta-btn.png);
  }
  


  .navbar-header:after{
    clear: none !important;
  }


  /* MEDIA QUERYS */

  @media (min-width: 480px) {

  }
  
  @media (min-width: 768px) {
    html{
      overflow: hidden;
    }

    .container-logo{
      margin-top: 75px;
      width: 100%;
    }
     .navbar-controles p {
        display: inline;
        color: #fff;
        margin: 20px 20px 0px 0px !important;
    }
    .navbar-controles span{
      display: initial;
      color: #fff;
      font-size: 3em;
    }
    .navbar-controles a,span{
      text-decoration: none;
    }
 
    .circle__content {
      font-size: 1.5em;
    }

    .footer {
      position: fixed;
      right: 0;
      bottom: 0;
      width: 50%;
      color: #85898c;
      text-align: center;
    }
  }

</style>



